@use "global" as *;
// メインビジュアル
#visArea_nihongo {
  background-color: #fff;
  position: relative;
  z-index: 0;
  max-width: 1366px;
  margin: 0 auto;
  overflow-y: hidden;
  padding: 0 0 95px;
  @include mq(sm) {
    padding: 0 0 0;
  }
  .mainArea__inner {
    max-width: 860px;
    margin: 0 auto;
  }
}

#visArea_nihongo .mainArea {
  background: url(img/mv.png) center top no-repeat;
  background-size: contain;
  min-height: 450px;
  position: relative;
  z-index: 0;
  padding: 45px 0 0;
  @include mq(sm) {
    background: url(img/mv_sp.png) center top no-repeat;
    background-size: 100% auto;
    padding: 20px 0 35px;
  }
}

#visArea_nihongo .mainArea h1 {
  @include mq(sm) {
    margin: 0 auto 10px;
  }
}

#visArea_nihongo .mainArea .mainArea__inner {
  @include mq(sm) {
    padding: 0 15px;
  }
}

#visArea_nihongo::before {
  position: absolute;
  content: "";
  width: 178px;
  height: 178px;
  z-index: -1;
  background: url(img/mv_left.png);
  top: -11%;
  left: 0;
  @include mq(sm) {
    content: none;
  }
}

#visArea_nihongo .subTitArea {
  display: block;
  @include mq(sm) {
    margin: 0 auto 16px;
    max-width: 285px;
  }
}

#visArea_nihongo .mainArea__subtit {
  text-align: center;
  margin: 0 auto 48px;
  max-width: 346px;
  padding: 5px;
  background: #ee8204;
  color: #fff;
  border-radius: 43px;
  position: relative;
  font-weight: bold;
  font-size: 18px;
  @include mq(sm) {
    font-size: 15px;
    padding: 8px 10px;
    margin: 0 auto;
    line-height: calc(42 / 30);
  }
}

#visArea_nihongo .mainArea__subtit:after {
  position: absolute;
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-left: 30px solid transparent;
  border-right: 30px solid transparent;
  border-top: 30px solid #ee8204;
  left: 50%;
  transform: translateX(-50%);
  bottom: -13%;
  z-index: -1;
}

#visArea_nihongo .mainTxtBox {
  text-align: center;
  margin: 0 auto 75px;
  @include mq(sm) {
    margin: 0 auto 66px;
  }
}

#visArea_nihongo .mainTxtBox p {
  margin: -12px auto;
  font-size: 18px;
  line-height: calc(26 / 18);
  @include mq(sm) {
    max-width: 235px;
    margin: 0 auto;
    font-size: 16px;
    line-height: calc(42 / 30);
  }
}

#visArea_nihongo .sitename {
  max-width: 440px;
  margin: 0 auto;
  line-height: 1;
  @include mq(sm) {
    max-width: 333px;
  }
}

#visArea_nihongo .mainTxtBox .sitename img {
  width: 100%;
  height: auto;
}

.heading_addTxt {
  padding: 30px 0 55px;
  background: url(img/hand_bg.png) center no-repeat;
  background-size: contain;
  @include mq(sm) {
    background: url(img/hand_bg.png) top 40px center no-repeat;
    background-size: 126px auto;
    padding: 0 0 60px;
  }
}

.heading_addTxt .heading_inner {
  max-width: 740px;
  margin: 0 auto;
  text-align: center;
  @include mq(sm) {
    padding: 0 15px;
    margin: -15px auto 0;
  }
}

.heading_addTxt .heading_inner p {
  font-size: 20px;
  line-height: calc(33 / 20);
  font-weight: bold;
  @include mq(sm) {
    font-size: 15px;
    line-height: calc(40 / 30);
    margin: 15px auto 11px;
  }
}

.heading_addTxt .heading_inner p + p {
  margin: -20px auto 0;
  @include mq(sm) {
    margin: 0;
  }
}

.headArea .ttl {
  font-size: 36px;
  max-width: 700px;
  margin: 0 auto 40px;
  text-align: center;
  color: #483c34;
  background: url(img/line_ico.png) no-repeat bottom;
  padding: 0 0 30px;
  @include mq(sm) {
    font-size: 18px;
    background-size: 23px auto;
    margin: 0 auto 24px;
    padding: 0 0 22px;
  }
}

.headArea .ttl span.small {
  display: block;
  margin: 0 0 16px 0;
  color: #ee8204;
  @include mq(sm) {
    font-size: 18px;
    margin: 0 0 8px 0;
  }
}

.headArea .ttl img {
  @include mq(sm) {
    max-width: 280px;
  }
}

.mainArea .frame-wrap {
  margin: 0 auto;
  max-width: 630px;
  display: block;
  position: relative;
}

.mainArea .frame-wrap::after {
  content: "";
  position: absolute;
  width: 5px;
  height: 68px;
  background: url(img/mv_dot.png) bottom no-repeat;
  bottom: -49%;
  left: 50%;
  transform: translateX(-50%);
  @include mq(sm) {
    background: url(img/mv_dot_sp.png) bottom no-repeat;
    background-size: 5px 40px;
    bottom: -54px;
    width: 5px;
    height: 40px;
  }
}

.mainArea .frame.type-c {
  background: #fff;
  border: 1px solid #483c34;
  border-radius: 30px;
  padding: 20px 25px 20px;
  display: flex;
  justify-content: space-between;
  position: relative;
  max-height: 150px;
  @media screen and (max-width: 480px) {
    padding: 8px 14px;
    flex-direction: column;
    position: relative;
    z-index: 0;
    border-radius: 15px;
    max-height: 100%;
  }
}

.mainArea .frame.type-c .txtArea {
  max-width: 400px;
  width: 100%;
  @media screen and (max-width: 480px) {
    margin: 0 auto -110px;
  }
}

.mainArea .frame.type-c .txt_box {
  font-size: 22px;
  font-weight: bold;
  padding: 0 0 0 90px;
  line-height: calc(32 / 22);
  @media screen and (max-width: 480px) {
    padding: 0 0 0 50px;
    font-size: 18px;
    line-height: calc(43 / 36);
    margin: 0 auto 27px;
  }
}

.mainArea .frame.type-c .frame-body {
  margin: -8px auto 0;
  @media screen and (max-width: 480px) {
    margin: -15px auto 0 0;
    padding: 0;
  }
}

.mainArea .frame.type-c .frame-body p {
  margin: 20px auto 0;
  @include mq(sm) {
    margin: 20px auto;
  }
}

.mainArea .frame.type-c .frame-body .btn-internal a {
  @include mq(sm) {
    border: 2px solid #fff;
  }
}

.mainArea .frame.type-c .img_box {
  margin: -12px -25px -40px 0;
  @media screen and (max-width: 480px) {
    max-width: 111px;
    margin: 0 -14px -8px auto;
    position: relative;
    z-index: -1;
  }
}

.mainArea .frame.type-c:before {
  content: "";
  width: 81px;
  height: 82px;
  position: absolute;
  background: url(img/bubble01.png) left center no-repeat;
  background-size: contain;
  top: -5%;
  @media screen and (max-width: 480px) {
    background: url(img/bubble01_sp.png) left center no-repeat;
    background-size: 50px auto;
    width: 50px;
    height: 50px;
    left: 3%;
    top: -11%;
  }
}

.mainArea .frame.type-c .btn-internal a {
  max-width: 335px;
  @media screen and (max-width: 480px) {
    max-width: 210px;
    margin: 0;
    padding: 8px 30px;
  }
}

// mainVisual_lower
.visArea_lower {
  min-height: 260px;
  margin: 0 auto;
  position: relative;
  z-index: 0;
  @media screen and (max-width: 480px) {
    background: #f6f9e3;
    min-height: 100%;
  }
  .mainArea {
    max-width: 1366px;
    margin: 0 auto;
    text-align: center;
    padding: 80px 0 60px;
    min-height: 120px;
    position: relative;
    overflow-y: clip;
    @media screen and (max-width: 480px) {
      padding: 20px 0 18px;
      min-height: 100%;
      background: url(img/lower-bg_sp.png) top no-repeat;
      background-size: 100% auto;
    }
    &:before {
      content: "";
      display: block;
      position: absolute;
      background: url(img/lower-bg--left.png) top no-repeat;
      background-size: auto 100%;
      left: -50px;
      top: -20px;
      width: 477px;
      height: 325px;
      z-index: -1;
      @media screen and (max-width: 480px) {
        content: none;
      }
    }
    &:after {
      content: "";
      display: block;
      position: absolute;
      background: url(img/lower-bg--right.png) top no-repeat;
      background-size: auto 100%;
      right: 35px;
      bottom: -20px;
      width: 258px;
      height: 254px;
      z-index: -1;
      @media screen and (max-width: 480px) {
        content: none;
      }
    }
    .sitename {
      font-size: 36px;
      font-weight: bold;
      @media screen and (max-width: 480px) {
        font-size: 18px;
      }
      img {
        @media screen and (max-width: 480px) {
          max-width: 260px;
        }
      }
    }
  }
}

